.sceleton {
  width: 100%;
  height: 50px;
  position: relative;
  box-shadow: 0 1px 5px 0 var(--border-card-box-shadow);
  overflow: hidden;

  &::after {
    content: '';
    display: block;
    position: absolute;
    left: -150px;
    top: 0;
    height: 100%;
    width: 80%;
    background: linear-gradient(to right,
        transparent 0%,
        var(--border-card-box-shadow) 50%,
        transparent 100%);
    animation: load 1s cubic-bezier(0.4, 0, 0.2, 1) infinite;
  }
}

@keyframes load {
  from {
    left: -150px;
  }

  to {
    left: 100%;
  }
}
